<template lang="pug">
    v-list-item
        v-list-item-content
            v-row
                v-col(cols="12" sm="12" lg="4")
                    h3 地址:{{address}}
                v-col(cols="12" sm="12" lg="4")
                    h3 {{getusername}}{{username}}
                v-col(cols="12" sm="12" lg="4")
                    h3 余额:{{balance}}
                v-col(cols="12" sm="12" lg="12")
                    v-divider
        v-list-item-action
            v-btn(color="success" dark @click="getmore") 详情
</template>

<script>
import * as api from "@/apis/wswebapi"
export default {
    name: "tokenlistitem",
    props:{
        address:{
            type:String,
            default:"",
        },
        // username:{
        //     type:String,
        //     default:"",
        // },
        balance:{
            type:Number,
            default:0,
        },
    },
    data: () => ({
        username:"未定义"
    }),
    computed: {
        getusername(){
        this.updateusername();
        return "用户名:" 
        }
    },
    methods:{
        getmore(){
            this.$store.dispatch('user/update_trxhistoryaddress',this.address);
            this.$router.push({path:"/trx/trc10history"});
        },
        updateusername(){
            api.GetUserNameByAddress({address:this.address}).then(res=>{
                if (res.code==200){
                    this.username=res.data;
                }else{
                    this.username=res.msg+res.error;
                }
            })
        },
        
    },
}
</script>


